:root {
  --rad: .7rem;
  --dur: .3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, .43, 1.49);
}

body {
  margin: 0;
}

html {
  box-sizing: border-box;
  height: 100%;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

form {
  width: 30rem;
  background: var(--color-brand);
  border-radius: var(--rad);
  position: relative;
}

input, .searchbar-button {
  height: var(--height);
  font-family: var(--font-fam);
  color: var(--color-dark);
  border: 0;
  font-size: 1.8rem;
}

input[type="search"] {
  width: 100%;
  background: var(--color-light);
  border-radius: var(--rad);
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition: all var(--dur) var(--bez);
  z-index: 1;
  outline: 0;
  padding: 0 1.6rem;
  transition-property: width, border-radius;
  position: relative;
}

.searchbar-button {
  width: var(--btn-width);
  background: var(--color-brand);
  border-radius: 0 var(--rad) var(--rad) 0;
  font-weight: bold;
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}

input:not(:placeholder-shown) {
  border-radius: var(--rad) 0 0 var(--rad);
  width: calc(100% - var(--btn-width) );
}

input:not(:placeholder-shown) + .searchbar-button {
  display: block;
}

label {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.searchbar {
  height: 60px;
  width: 100%;
  background-color: #6495ed;
  justify-content: center;
  align-items: center;
  display: flex;
}

.searchbar__input {
  height: 30px;
  width: 300px;
}

button:hover, button:focus {
  cursor: pointer;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: 400px;
  height: 450px;
}

.photo-preview {
  width: 400px;
  height: 360px;
  object-fit: cover;
}

.info {
  height: 90px;
  border: 1px solid #000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
  font-size: 20px;
  display: flex;
}

.info-photo {
  height: 360px;
  display: block;
}

.info-item {
  text-align: center;
}

.button-block {
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.load-more {
  width: 140px;
  height: 40px;
  background: var(--color-brand);
  font-weight: bold;
  font-family: var(--font-fam);
  border: 0;
  border-radius: 15px;
  font-size: 1.8rem;
}

